import string from './css.js'
const demo= document.querySelector('#demo')
const demo2= document.querySelector('#demo2')
let n = 1
let time = 10
let id

// å°†å‡ ä¸ªå‡½æ•°æ”¾å¯¹è±¡ä¸­
const player = {
    init: () => {
        demo.innerText = string.substring(0,n)
        demo2.innerHTML = string.substring(0,n)
        player.play() // id = player.play() // æ¯æ¬¡è°ƒç”¨playæ—¶éƒ½æŠŠplayçš„è¿”å›žå€¼ç»™äº†idï¼Œæ‰€ä»¥å¯ä»¥å†…ç½®
    },
    run: ()=> {
        n += 1
        if( n > string.length ){
            window.clearInterval(id)
            return
        }  
        // console.log(n + ':' + string.substring(0,n));
        demo.innerText = string.substring(0,n)
        demo2.innerHTML = string.substring(0,n)
        demo.scrollTop = demo.scrollHeight // æ‹–åŠ¨æ»šåŠ¨æ¡ï¼Œå¾—åˆ°demoçš„é«˜åº¦ demo.scrollHeight //4014
    },
    play: () => {
        id = setInterval(player.run,time) // å†…ç½®
    },
    pause: () => {
        window.clearInterval(id)
    },
    slow: () => {
        player.pause()
        time = 100
        player.play()
    },
    normal: () => {
        player.pause()
        time = 50
        player.play()
    },
    fast: () => {
        player.pause()
        time = 0
        player.play() 
    }
}
player.init()

document.querySelector('#btnPause').onclick = player.pause
document.querySelector('#btnPlay').onclick = player.play
document.querySelector('#btnSlow').onclick = player.slow
document.querySelector('#btnNormal').onclick = player.normal
document.querySelector('#btnFast').onclick = player.fast

